.quiz-list-wrapper {
  max-width: 800px;
  margin: 20px auto;
}

.quiz-list {
  list-style: none;
  padding: 0;
}

.quiz-item {
  border-bottom: 1px solid var(--accent-color);
  font-size: 1.5em;
  font-weight: 400;
  text-align: center;
  background-color: rgb(199, 168, 118, 0.05);
  border-radius: 3px;
  margin-bottom: 15px;
}

.quiz-item:hover {
  background-color: rgb(199, 168, 118, 0.4);
}

.quiz-item a {
  padding: 15px 10px;
  text-decoration: none;
  display: inline-block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.quiz-item a:hover {
  text-decoration: none;
}

.chapter {
  font-size: 1.2em;
}

.title {
  color: #777777;
}

@media (max-width: 575px) {
  .quiz-item {
    font-size: 0.9em;
  }
}